import { Component } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { HttpClient } from '@angular/common/http';

import { HTTP } from '@ionic-native/http';
import { OneSignal } from '@ionic-native/onesignal';



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public error : any = '';
  public p : any = [];
  public mode : any = 0;
  public myname : any = '';
  public started : any = 0;
  public options : any[];
 public tick : any = './assets/img/tick.png';

  public blank : any = './assets/img/blank.png';


  constructor(public navCtrl: NavController, public loadingCtrl : LoadingController, private geolocation: Geolocation, private http: HttpClient, public nhttp : HTTP, private oneSignal: OneSignal) {
    this.p = { email : '', pass : '', empcode : '', timezn : '', locn : 'Unknown', lat : '', lng : '', me : 0, job : 0};
    this.options = [];
  }

 getpushinfo()
  {
     this.oneSignal.startInit('07718501-db1c-4b51-ade6-85b3155bab32', '126894144681'); 
 
     this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
     this.oneSignal.endInit();
     let i = this.oneSignal.getIds();
     i.then( data => {

        let w = window.localStorage.getItem('me');
        window.localStorage.setItem('pushid',data.userId);

        this.http.get('http://jaydenkur.com.au/giovenco/server/pushtoken.php?me='+w+'&pushid='+data.userId).subscribe(data2	=>	{	
        });
     });
  }     


  ionViewDidLoad() {
     let that = this;
     let me = window.localStorage.getItem('me');
     if (!me) me='';
     if (me) {
       this.mode=1;
       this.myname = window.localStorage.getItem('myname');
       this.getpushinfo();
       this.pinme();
     }
     else this.mode=0;
  }

  checkeml()
  {
         this.error='';
        let that = this;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checkeml.php?eml='+this.p.email+'&pwd='+this.p.pass).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                if (d.num) {
                  window.localStorage.setItem('myname',d.myname);
                  window.localStorage.setItem('me',d.num); 
                  this.mode=1; 
                  this.myname=d.myname;
                  that.getpushinfo();  
                  that.pinme();
                }
                else {
                  this.error='Incorrect - Please Try Again';
                }
        });


  }

  pinme()
  { let that=this;
    let d = new Date();
    let pp = d.toString();
    let a = pp.indexOf('GMT');
    
    this.p.timezn = pp.substr(a);
    this.p.me = window.localStorage.getItem('me');
this.geolocation.getCurrentPosition().then((resp) => {
   that.p.lat = resp.coords.latitude;
   that.p.lng = resp.coords.longitude;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checklocn.php?me='+window.localStorage.getItem('me')+'&lat='+that.p.lat+'&lng='+that.p.lng).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                that.p.locn = d.locn;
                that.started = d.started;
                that.options = d.options;
                that.p.job = d.job;
        });


}).catch((error) => {
        that.p.lat=0;
        that.p.lng=0;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checklocn.php?me='+window.localStorage.getItem('me')+'&noloc=1').subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                that.p.locn = d.locn;
                that.started = d.started;
                that.options = d.options;
                that.p.job = d.job;
        }); }
 );
}

 logout()
 { window.localStorage.removeItem('me');
   window.localStorage.setItem('myname','');
   this.mode=0;
 }
 
 chooseOption(a) {
    this.p.job = a;
    for (var i=0; i< this.options.length; i++) {
      if (this.options[i].id == a) this.options[i].selected = 1;
      else this.options[i].selected=0;
    }
  }

 startShift()
 {     let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://jaydenkur.com.au/giovenco/server/startshift.php';

         this.nhttp.post(url, that.p, {})
         .then(data => {
            loading.dismiss();
 	    that.started=1;
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
 }
  
 endShift()
 {
     let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://jaydenkur.com.au/giovenco/server/endshift.php';

         this.nhttp.post(url, that.p, {})
         .then(data => {
            loading.dismiss();
 	    that.started=0;
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });

 }

}



